import Layout from "./components/myLayoutBefore";
import React from 'react';
import {Alert} from './components/warnAlert';
import Alert1 from './components/successAlert';
import {Post} from "./components/http";
import "./css/app.css";
import { Privacy } from "./components/Privacy";
import bcrypt from "bcryptjs";

const inputStyle ={
    margin: 20,
    outlineStyle: 'none' ,
    border: '1px solid #ccc',
    borderRadius: '3px',
    padding: '12px 12px',
    width: '70%',
    fontFamily: "Microsoft soft",
    
}
const boxStyle={
    position:'relative',
    left:'20%',
    textAlign:'center',
    width:"60%",
    marginBottom:'20px',
    padding:'30px',
    border:'2px solid  #ffd633',
    borderRadius:'2px',
 
}

const bannerStyle ={
    position:'relative',
    left:'20%',
    textAlign:'center',
    marginTop:"20px",
    width:"60%",
    padding: '10px 10px',
    border:'2px solid  #ffd633',
    backgroundColor:"#ffd633",
    borderRadius:'2px',
   
}

var salt = bcrypt.genSaltSync(10);

